body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.interface-ctn {
    margin-left: 50px;
    margin-top: 10px;
    display:flex;
    flex-flow: column nowrap;
}

.board {
    display:flex;
	width:330px;
    flex-flow: row wrap;
    border:1px solid black;
}

.btn-ctn {
    margin-top: 20px;
    width: 330px;
    display: flex;
    justify-content: space-between;
}
.main-btn {
    width: 100px;
    border-radius: 12px;
    outline: none;
}

.slider {
    width: 30%;
    float: center;
    margin-top: 20px;
    margin-left: 22%;
}
@media only screen and (max-width: 600px){
    .slider {
        margin-left: 50%;
        width: 100%;
    }
}
.cell {
    border: 1px solid black;
    height: 20px;
    width: 20px;
    align-content: center;
    justify-content: center;
}

.true {
    background-color: salmon;
}
.App {
  text-align: center;
  max-width: 1024px;
  height: 800px;
  margin: 0 auto;
  border: solid black 2px;
  font-size: 2.0rem;
}
.main-ctn {
  display: flex;
}
.board-ctn {
  width: 50%;
}
.rules-ctn {
  width: 50%;
  margin-top: 20px;
  margin-right: 30px;
  font-size: 1.2rem;
}
.rule-item {
  margin-top: 47px;
}

@media only screen and (max-width: 600px){
  .App {
    border: none;
  }
  .main-ctn{
    flex-flow: column nowrap;
  }
  .rules-ctn {
    width: 100%;
  }
}
